import React,{useState} from "react";
import styles from "./TodoCreate.module.css"


export default function TodoCreate(props) {
  const [todoItem, setTodoItem] = useState('');
  const { addData } = props;
  const [idData, setIdData] = useState('')
  

  //   
  const currentDate = () => {
    let newDate = new Date();
    let date = newDate.getDate();
    let month = newDate.getMonth() + 1;
    let year = newDate.getFullYear();

    if (date < 10) date = '0' + date;
    if (month < 10) month = '0' + month;

    return year + ` ` + month + ` ` + date + ``;
  }

  // todo 
  const createTodo = (e) => {
    setTodoItem(e.target.value);
  }

  //  
  const clickBtnCreate = () => {

    if (todoItem.length === 0) {
      return alert('  .');
    }

    addData({
      id: String(new Date()),
      todoItem: todoItem,
      checking: false
    });
    resetInput()

  }

  const resetInput = () => {
    setTodoItem('');

  }

  return (
    <div className={styles.todo_create}>
      <div className={styles.today_date}>{currentDate()}</div>
      <div className={styles.input_area}>
        <input className={styles.input_todo} type="text" placeholder="  ." onChange={createTodo} value={todoItem}></input>
        <button className={styles.btn_create} onClick={clickBtnCreate}></button>
      </div>
    </div>
  )
}