import React, { useRef, useState } from "react";
import { useHistory } from "react-router-dom/cjs/react-router-dom.min";
import useFetch from "../hooks/useFetch";
import styles from "./CreateWord.module.css"


export default function CreateWord(props) {
  
  const days = useFetch("http://localhost:3001/days");
  const engRef = useRef(null); //useRef     .
  const korRef = useRef(null);
  const dayRef = useRef(null);
  const history = useHistory(); // react-router  
  const [isLoading, setIsLoading] = useState(false);
  
  const dayOption = days.map((day) => {
    return (
      <option key={day.id} value={day.day}>
        {day.day}
      </option>
    )
  });

  // console.log('[[[[[',location.state.data)
  const onSubmit = (e) => {
    e.preventDefault();
    // console.log(engRef.current.value); // ...
    // console.log(korRef.current.value);
    // console.log(dayRef.current.value);
    if (engRef.current.value.length === 0) {
      return alert('  .')
    }
    if (korRef.current.value.length === 0) {
      return alert('  .')
    }
    if (!isLoading) {
      setIsLoading(true);
      fetch('http://localhost:3001/words/', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          day: dayRef.current.value,
          eng: engRef.current.value,
          kor: korRef.current.value,
          isDone: false
        })
      })
        .then(res => {
          if (res.ok) {
            // console.log('click');
            setIsLoading(false); //()
            // alert('  .');
            history.push('/voca/day/' + dayRef.current.value); //   / Link to  a     
          }
        })
    }

  }
  
  const cancel = () => {
    history.goBack();
  }
  const saveText = isLoading ? "Saving..." : "";

  return (
    <>
    <form onSubmit={onSubmit} className={styles.CreateWord_form}>
      <div className={styles.input_area}>
        <label>Eng</label>
        <input type="text" placeholder="apple" ref={engRef} />
      </div>
      <div className={styles.input_area}>
        <label>Kor</label>
        <input type="text" placeholder="" ref={korRef} />
      </div>
      <div className={styles.input_area}>
        <label>Day</label>
        <select ref={dayRef}>
          {dayOption}
        </select>
      </div>
      <button type="submit" disabled={isLoading}>{saveText} {isLoading}</button>
    </form>
    <button onClick={cancel} className={styles.btn_cancel}></button>
    </>
  )
}